export default class FormAnalitics {
  constructor(props) {
    super(props);
    this.state = {
      user_name: currentUser.accountId,
      app_id: 'App example',
      action_id: 'Action example'
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({
      user_name: event.target.user_name,
      app_id: event.target.app_id,
      action_id: event.target.action_id
    });
  }

  handleSubmit(event) {
    console.log('Will set analitics into blockchain: ' + this.state);
    contract.set_analytics(this.state);

    let record = contract.get_analytics();
    console.log('Get from blockchain: ' + record);
    alert(record);
    event.preventDefault();
  }

  render() {
    return (
      <form onSubmit={onSubmit}>
        <fieldset id="fieldset">
          <p>Sign into log analitics, { currentUser.accountId }!</p>
          <p className="highlight">
            <label htmlFor="user_name">User name</label>
            <input
              autoComplete="off"
              autoFocus
              id="user_name"
              value="{ this.state.user_name }"
              required
            />
          </p>
          <p>
            <label htmlFor="app_id">App #</label>
            <input
              autoComplete="off"
              autoFocus
              id="app_id"
              value="{ this.state.app_id }"
              required
            />
          </p>
          <p>
            <label htmlFor="action_id">Action #</label>
            <input
              autoComplete="off"
              autoFocus
              id="action_id"
              value="{ this.state.action_id }"
              required
            />
          </p>
          <button type="submit">
            Submit
          </button>
        </fieldset>
      </form>
    );
  }
}

Form.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  currentUser: PropTypes.shape({
    accountId: PropTypes.string.isRequired,
    balance: PropTypes.string.isRequired
  })
};
